import React, {useState} from 'react';
import Box from '@mui/material/Box';
import { useTranslation } from 'react-i18next';
import List from '@mui/material/List';
import GitHubIcon from '@mui/icons-material/GitHub';
import CallIcon from '@mui/icons-material/Call';
import EmailIcon from '@mui/icons-material/Email';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import { useThemeContext } from '../Burger/themeContext';
import themes from './../Burger/theme';
import Typography from '@mui/joy/Typography';
import TextField from '@mui/material/TextField';
import { DarkMode } from '@mui/icons-material';
import Button from '@mui/joy/Button';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { styled } from '@mui/system';
import emailjs from '@emailjs/browser';
import Alert from '@mui/material/Alert';
import AlertTitle from '@mui/material/AlertTitle';

const Contact = () => {
  const { t } = useTranslation();
  const {darkMode} = useThemeContext();

  const icons = (
    <List sx={{display:"flex", flexWrap:"wrap", width: '200px', justifyContent: 'space-between'}}>
        <GitHubIcon />
        <CallIcon />
        <EmailIcon />
        <LinkedInIcon />
    </List>
  )

  const CustomButton = styled(Button)({
    backgroundColor: themes.dark.primaryColor,
    borderColor: themes.dark.primaryColor,
    margin: '2%',
    '&:hover': {
      borderColor: '#754de8',
      backgroundColor: '#000000',
    },
  });

  const validationSchema = Yup.object({
    firstName: Yup.string()
      .max(15, 'Doit tre 15 caractres ou moins')
      .required('Champ requis'),
    lastName: Yup.string()
      .max(20, 'Doit tre 20 caractres ou moins')
      .required('Champ requis'),
    email: Yup.string()
      .email('Email invalide')
      .required('Champ requis'),
    message: Yup.string()
      .required('Champ requis'),
  });
  
  const MyForm = () => {
    const [showSuccessAlert, setShowSuccessAlert] = useState(false);

    const formik = useFormik({
      initialValues: {
        firstName: '',
        lastName: '',
        email: '',
        message: '',
      },
      validationSchema: validationSchema,
      onSubmit: (values) => {
        emailjs.send('service_o4il1by', 'template_n1x56ie', values, 'K7q1JB9_2et-A-NdO')
          .then((response) => {
            console.log('E-mail envoy avec succs!', response);
            formik.resetForm();
            setShowSuccessAlert(true);
          setTimeout(() => {
            setShowSuccessAlert(false);
          }, 5000);
          })
          .catch((error) => {
            console.error('Erreur lors de l\'envoi de l\'e-mail:', error);
          });
      },
    });
  
    return (
      <>
      {showSuccessAlert && (
        <Alert severity="success">
          <AlertTitle>Success</AlertTitle>
          Le mail a t envoy avec succs!
        </Alert>
      )}
      <form onSubmit={formik.handleSubmit}>
        <CustomTextField
          fullWidth
          id="firstName"
          name="firstName"
          label="Prnom *"
          value={formik.values.firstName}
          onChange={formik.handleChange}
          error={formik.touched.firstName && Boolean(formik.errors.firstName)}
          helperText={formik.touched.firstName && formik.errors.firstName}
        />
        <CustomTextField
          fullWidth
          id="lastName"
          name="lastName"
          label="Nom *"
          value={formik.values.lastName}
          onChange={formik.handleChange}
          error={formik.touched.lastName && Boolean(formik.errors.lastName)}
          helperText={formik.touched.lastName && formik.errors.lastName}
        />
        <CustomTextField
          fullWidth
          id="email"
          name="email"
          label="Email *"
          value={formik.values.email}
          onChange={formik.handleChange}
          error={formik.touched.email && Boolean(formik.errors.email)}
          helperText={formik.touched.email && formik.errors.email}
        />
        <CustomTextField
        fullWidth
          id="message"
          name="message"
          label="Message *"
          value={formik.values.message}
          onChange={formik.handleChange}
          error={formik.touched.message && Boolean(formik.errors.message)}
          helperText={formik.touched.message && formik.errors.message}
          multiline
          rows={5}
        />
        <CustomButton type="submit" size="lg" variant="solid" sx={{marginRight: '20px'}}>Envoyer</CustomButton>
      </form>
      </>
    );
  };

  const CustomTextField = (props) => {
    return (
      <TextField
        {...props}
      InputLabelProps={{
        style: {
          color: DarkMode ? themes.dark.text : themes.light.primaryColor,
        },
      }}
        sx={{
          margin: '2%',
          '& .MuiOutlinedInput-root': {
            '& fieldset': {
              borderColor: 'var(--TextField-brandBorderColor)',
              color: DarkMode? themes.dark.primaryColor : themes.light.primaryColor,
            },
            '&:hover fieldset': {
              borderColor: 'var(--TextField-brandBorderHoverColor)',
            },
            '&.Mui-focused fieldset': {
              borderColor: 'var(--TextField-brandBorderFocusedColor)',
              color: DarkMode? themes.dark.primaryColor : themes.light.primaryColor,

            },
          },
        }}
      />
    );
  };

  return (
    <Box
      component="main"
      sx={{
        flexGrow: 1,
        p: 3,
        backgroundColor: darkMode ? themes.dark.backgroundColor : themes.light.backgroundColor,
        minWidth: '100%',
        color: darkMode ? 'white' : '#000000',
      }}
    >
      <Box sx={{ height: '100%', margin: '3%', bgcolor: '#171717', borderRadius: '10px', padding: '3%'}}>
          <Typography style={{ color: darkMode? themes.dark.text : themes.light.textColor, textAlign: 'center', marginBottom: '15px', fontSize: '2.6rem' }}>{t('CONTACT')}</Typography>
          <Box sx={{display:'flex', flexWrap:'wrap', justifyContent:'space-around'}}>
          <Box sx={{margin: '3%', width: '100%', maxWidth: '70%', '@media (min-width:700px)': { maxWidth: '30%' }}}>
          <MyForm />
          </Box>


        <Box sx={{margin: '3%'}}>
        <Typography sx={{fontSize: '1.8rem', color: darkMode? themes.dark.text : themes.light.textColor,}}><LocationOnIcon></LocationOnIcon> EMPLACEMENT</Typography><br></br>
        <p>Valbonne Sophia-Antipolis, 06560</p><br></br><br></br>
        <h2>
            Mes Informations
        </h2>
        <br></br>
        {icons}
        </Box>
        </Box>
        </Box>
      </Box>
  );
};

export default Contact;

